	module.exports = {"data":{"markdownRemark":{"html":"<p>Da det syvende heftet med Lyriske stykker kom ut i 1895, var Grieg blitt 52 år. Vi vet ikke sikkert om alle stykkene er skrevet i 1895 eller om de kan ha blitt til mellom 1893 og 1895.</p>\n<p>Vinteren 1894-1895 oppholdt Grieg seg i København. Helsen var dårlig og Grieg var plaget av lungebetennelse. En planlagt tur til Leipzig gikk derfor i vasken, og Grieg skrev til sin venn Frants Beyer i Bergen:  </p>\n<blockquote>\n<p>”Tyngsel, slapphet, åndeløshet, mangel på energi, her har du mine fornemste egenskaper for tiden” - (Brev til Beyer 29. mars 1895).</p>\n</blockquote>\n<p>Men på tross av liten energi gjorde Grieg ferdig ”To nordiske melodier”, op. 63, for strykeorkester, og arbeidet med stykkene som ble utgitt som Lyriske Stykker VII, op. 62. Denne samlingen hører ikke til hans mest kjente, men inneholder mange perler.</p>\n<p>I begynnelsen av mai 1895 kom han til Troldhaugen, og en måned etter sendte han det ferdige manuskriptet til Lyriske stykker VII, op. 62 til forleggeren, og ba ham om selv å bestemme honorar. Forlagshuset Peters tjente godt på de lyriske stykkene; hver gang de mottok et nytt hefte med lyriske stykker fra Grieg, gikk flagget til topps.</p>\n<p>I «Sylfide», nr. 1, føyer Grieg seg inn den romantiske tradisjonen, vi aner kanskje litt Chopin her. Stykket er ikke spesielt virtuost, men de små temposkiftene skaper liv og variasjon.</p>\n<p>Manuskriptet til neste stykke, «Takk», er datert 11. januar 1895. Grieg er i København, og noen uker etter dirigerer han sine egne verk på en konsert. Konserten var en suksess og alle de 2000 plassene var utsolgt. Takk er et enkelt stykke og kan ligne på en koral. Vi vet ikke helt hva Grieg ønsker å takke for her, det får vi overlate til fantasien.</p>\n<p>«Fransk serenade» har en spesiell kommentar fra Grieg til forleggeren, der han snakker om at stykket er lite og sjarmerende – akkurat som de franske damene! Stykket er melodiøst og elegant; Grieg har lekt seg med salongmusikken, og har lyktes svært godt.</p>\n<p>«Bekken» er nok det mest kjente stykket fra denne samlingen, virtuost og teknisk vanskelig. Det har vært hevdet at dette stykket – ved siden av ”I Dovregubbens Hall” – er Griegs mest karakteristiske rocke-låt. Selv om han ikke kjente til begrepet har han i alle fall hatt teft for å få det til å svinge.</p>\n<p>Med tempoangivelsen Poco Andante ed espressivo kommer «Drømmesyn» som en kontrast, med klangfarger og raske harmoniske skift til nye tonearter. Her er det ingen tekniske vanskeligheter for den som skal framføre det, men stykket inviterer likevel til konsentrasjon og ettertanke, for både utøver og lytter.</p>\n<p>I det siste stykket, «Hjemad» kjenner vi igjen Grieg på basstonene i åpningen og på det lekende og folkelige. Siden vi ikke vet når stykket er datert, kan vi spekulere på om det er hjemlengselen som har tatt ham. Etter en vinter med mye sykdom i København, skriver han til Frants Beyer i Bergen at han lengter etter å få oppleve vårstemningen på Troldhaugen, og håper at naturen vil gi ham arbeidslyst. Og så skriver han fornøyd om tanken på ”å eie en plett av jord der oppe” og ser fram til at han og Beyer skal få seg noen turer, ”som i gamle dager”. Det kan i alle fall høres ut som om han har hastverk med å komme hjem.</p>","timeToRead":2,"excerpt":"Da det syvende heftet med Lyriske stykker kom ut i 1895, var Grieg blitt 52 år. Vi vet ikke sikkert om alle stykkene er skrevet i 1895 eller…","id":"/Users/thomasheimstad/Sites/Obo/src/posts/opus62.md absPath of file >>> MarkdownRemark","frontmatter":{"workname":"Lyriske stykker hefte VII","composedin":"1895","startDate":"16.6","startTime":null,"location":"Grieghallen","artists":["Geir Henning Braaten"],"medialink":null,"title":"62","dedicatedTo":"","author":"Monica Jangaard","date":"2018-05-07T12:00:00.048Z","category":"opus","tags":["klaver"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":2.0023041474654377,"src":"/static/opus62-d6c9ad67986073743289b31c16899498-69160.jpg","srcSet":"/static/opus62-d6c9ad67986073743289b31c16899498-82221.jpg 480w,\n/static/opus62-d6c9ad67986073743289b31c16899498-69160.jpg 869w","sizes":"(max-width: 869px) 100vw, 869px"}}}},"fields":{"slug":"/opus62/","artist":[{"frontmatter":{"title":"Geir Henning Braaten","category":"artister","tags":["pianist"],"thumbnail":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1,"src":"/static/GeirHenningBraaten-14cfc5c14e2b3f08e989fb1705e1e2bf-466fa.jpg","srcSet":"/static/GeirHenningBraaten-14cfc5c14e2b3f08e989fb1705e1e2bf-27b23.jpg 90w,\n/static/GeirHenningBraaten-14cfc5c14e2b3f08e989fb1705e1e2bf-0afbd.jpg 180w,\n/static/GeirHenningBraaten-14cfc5c14e2b3f08e989fb1705e1e2bf-466fa.jpg 360w,\n/static/GeirHenningBraaten-14cfc5c14e2b3f08e989fb1705e1e2bf-43088.jpg 540w,\n/static/GeirHenningBraaten-14cfc5c14e2b3f08e989fb1705e1e2bf-30979.jpg 680w","sizes":"(max-width: 360px) 100vw, 360px"}}}},"fields":{"slug":"/artister/geir-henning-braaten/","opuses":[{"frontmatter":{"title":"62"}}]}}],"opuses":null}}},"pathContext":{"slug":"/opus62/"}}

